export default {
  head: {
    title: '聯絡 - 西打藍 接案陪跑 / 商業行銷顧問',
    meta: [
      { name: 'keywords', content: '西打藍, 西打藍官方網站, 自由工作者, 自由接案, 前端工程師, 文字工作者, 網站開發, 網站架設, 軟體工程師'},
    ]
  },
  layout: 'default',
  components: {
    boxContent: require('~/components/common/box-content.vue').default,
    preBox: require('~/components/common/pre-box.vue').default,
    headText: require('~/components/common/head-text.vue').default,
    contactBtn: require('~/components/common/contact-btn.vue').default,
  },
  props: {
    
  },
  data () {
    return {
      form: {
        name: '',
        mail: '',
        msg: '',
        assure: ''
      },
      isForm: {
        name: true,
        mail: true,
        msg: true,
        assure: true
      },
      isEmpty: false
    }
  },
  computed: {

  },
  methods: {
    focusInput (focus) {
      if (focus == 'name') {
        this.isForm.name = true
      } else if (focus == 'mail') {
        this.isForm.mail = true
      }  else if (focus == 'msg') {
        this.isForm.msg = true
      } else if (focus == 'assure') {
        this.isForm.assure = true
      } 
    },
    handleSubmit () {
      if (!this.form.name) {
        this.isForm.name = false
      }
      if (!this.form.mail) {
        this.isForm.mail = false
      }
      if (!this.form.msg) {
        this.isForm.msg = false
      }
      if (!this.form.assure) {
        this.isForm.assure = false
      }
      if (this.form.assure != '西打藍') {
        this.isForm.assure = false
      }
      if(!this.form.name || !this.form.mail || !this.form.msg || !this.form.assure || this.form.assure != '西打藍') {
        return
      }

      const formUrl = 'https://docs.google.com/forms/u/0/d/e/1FAIpQLSfd6NNOtLe5hhKNlVD7gt6QxASHqKbKOyBsVdnpargTeTuk9A/formResponse'
      const queryString = require('query-string')
      const q = queryString.stringifyUrl({
        url: formUrl,
        query: {
          'entry.1757970004': this.form.name,
          'entry.739774615': this.form.mail,
          'entry.1914748398': this.form.msg,
        }
      })
      this.$axios.post(q)
      .then(response => {

      })
      .catch(error => {

      })

      window.scroll(0,0)
      this.isEmpty = true
    }
  },
  watch: {
    
  }
}
